

.splash-screen {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* background-color: black; */
    background: linear-gradient(180deg, rgba(2,0,36,1) 0%, rgba(3,169,206,1) 98%, rgba(0,212,255,1) 100%);
    z-index: 9999;
    font-family: "Nanum Pen Script";
    color:white;
    font-weight: bold;
    font-size: 50px;
  }
  
  .splash-logo {
    width: 150px;
    height: 150px;
  }
  